html {
  overflow-x: hidden;
}

body {
  margin     : 0;
  color      : #212121;
  overflow-x : hidden;
  font-family: 'Roboto', sans-serif;
}

div {
  white-space: pre-wrap;
}
/* ---------------------------------- IDs ----------------------------------- */
#Home {
  display         : table;
  width           : 100%;
  height          : 100vh;
  background-color: #E0F7FA;
  vertical-align  : middle;
}

#Navbar {
  position     : fixed;
  width        : 100%;
  opacity      : 0.99;
  border-bottom: solid 1px;
}

#search-input {
  width           : 160px;
  height          : 22px;
  border          : none;
  border-bottom   : solid 1px #212121;
  background-color: inherit;
  outline         : none;
  text-align      : center;
  font-size       : 15px;
}

#diamond-svg {
  width     : 100%;
  height    : 100vh;
  max-height: 800px;
}

#scroll-nav {
  position      : absolute;
  bottom        : 0;
  max-height    : 86px;
  padding-bottom: 10px;
}

#gpa-text {
  opacity: 0;
}

#gpa-text:hover {
  opacity: 1;
}
/* -------------------------------- Classes --------------------------------- */
.circle {
  border-radius: 100px;
}

.paper {
  padding: 30px;
}

.box-shadow,
.circle,
.paper {
  box-shadow: rgba(0, 0, 0, 0.12) 0 1px 6px, rgba(0, 0, 0, 0.12) 0 1px 4px;
}

.bold {
  font-weight: 500;
}

.lg-text,
.md-text,
.sm-text,
.xsm-text {
  line-height: 18px;
}

.xlg-text {
  font-size  : 26px;
  font-weight: 600;
}

.lg-text {
  font-size  : 20px;
  font-weight: 500;
}

.md-text {
  font-size  : 18px;
  font-weight: 500;
}

.sm-text {
  font-size  : 16px;
  font-weight: 400;
}

.xsm-text {
  font-size  : 14px;
  font-weight: 400;
  line-height: 20px;
}

.border-right {
  border-color: #757575;
  border-right: solid 1px;
}

::-webkit-scrollbar {
  display: none;
}

.selection-container {
  z-index   : 2;
  position  : relative;
  overflow-x: scroll;
}

.selection-content-container,
.selection-content-container-image {
  max-width: 450px;
}

.selection-content-diamond {
  width: 20px;
}

.selection-content-item {
  width     : calc(100% - 20px);
  text-align: left;
}

.selection-option {
  width  : 169px;
  padding: 25px 0 20px;
}

.default-pointer {
  cursor: default;
}

.inline-block {
  display: inline-block;
}

.tech-list-item {
  float        : left;
  padding      : 7px 10px;
  border-radius: 12px;
  border: #00BCD4 1px solid;
  font-size    : 14px;
}

.nav-height {
  z-index: 100;
  height : 44px;
}

.logo-svg-email {
  min-width  : 18px;
  min-height : 18px;
  margin-left: -25px;
  position   : absolute;
  padding-top: 12px;
}

.logo-svg-linkedin {
  min-width  : 17px;
  min-height : 17px;
  margin-left: -24px;
  position   : absolute;
  padding-top: 13px;
}

.logo-svg-git {
  min-width  : 26px;
  min-height : 26px;
  margin-left: -29px;
  position   : absolute;
  padding-top: 8px;
}

.logo-svg-music {
  min-width  : 21px;
  min-height : 21px;
  margin-left: -27px;
  position   : absolute;
  padding-top: 11px;
}

.full-inline-block {
  display   : inline-block;
  width     : 100%;
  min-height: 1px;
}

.height-100 {
  height: 100%;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.white-text {
  color: #FAFAFA;
}

.text-center {
  text-align: center;
}

.header {
  width           : 100%;
  height          : 40px;
  padding         : 15px 0;
  color           : #FAFAFA;
  background-color: #212121;
}

.section {
  display: table;
  margin : 0 auto;
  padding: 45px 0;
}

.center {
  display: table;
  margin : 0 auto;
}

.table-cell {
  display       : table-cell;
  vertical-align: middle;
}

.center-vertically-container {
  display : table;
  position: relative;
  height  : 100%;
}

.center-vertically {
  display       : table-cell;
  vertical-align: middle;
}

.no-select {
  user-select: none;
}

.hand {
  cursor: pointer;
}

.width-70 {
  width: 70%;
}

.width-80 {
  width: 80%;
}

.width-100 {
  width: 100%;
}

.text-left {
  text-align: left;
}

.underline {
  text-decoration: underline;
}
/* -------------------------------- Desktop --------------------------------- */
@media (min-width : 768px) {
  #mobile-navbar {
    display: none;
  }

  .section-width {
    width: 76%;
  }

  .selection-scroll {
    width     : 510px;
    overflow-x: scroll;
  }

  .selection-date-text {
    font-size  : 14px;
    font-weight: 400;
  }
}
@media (min-width : 512px) {
  .desktop-width-60 {
    width: 60%;
  }
}
/* --------------------------------- Mobile --------------------------------- */
@media (max-width : 768px) {
  #desktop-navbar {
    display: none;
  }

  #mobile-navbar {
    width: 100%;
  }

  .hamburger {
    padding-top: 5px;
  }

  .section-width {
    width: 90%;
  }

  .selection-date-text {
    font-size  : 12px;
    font-weight: 400;
  }
}
@media (max-width : 512px) {
  .full-mobile-width {
    min-width: 100%;
  }

  .hamburger {
    padding-top: 7px;
  }

  .selection-scroll {
    width     : 350px;
    overflow-x: scroll;
  }

  .selection-content-container-image {
    max-width: 300px;
  }
}
@media (max-width : 400px) {
  .selection-scroll {
    width     : 300px;
    overflow-x: scroll;
  }

  .selection-content-container-image {
    max-width: 250px;
  }

  .line-height-30 {
    line-height: 30px;
  }
}
